import { groupsDetail,Addgroup } from '@/api/index'
import util from "@/util/util"
export default {
    name: 'applyEntry', // 
    data() {
        return {
            // 
            id:'',//id
            name:'',//
            imgurl:'',//
            num:'',//
            applyTxt:'',
            mask:false,
            toastTxt:'',
            showQrcode:false
        }
    },
    watch: {
        // 
    },
    computed:{
        // showQrcode(){
        //     if(this.$route.meta.showQrcode){
        //         return true
        //     }else{
        //         alert('f');
        //         return false
        //     }
        // }
    },
    props: {
        // 
    },
    beforeCreate() {
        // console.group('  ===============beforeCreate');
    },
    created() {
        // console.group('===============created');
    },
    beforeMount() {
        // console.group('  ===============beforeMount');
    },
    mounted() {
        let that=this;
        this.id=this.$route.query.id;
        groupsDetail(this.id).then(res =>{
            that.imgurl=res.data.imgUrl;
            that.name=res.data.groName;
            that.num=res.data.userCount;
            if(res.data.isConcern==0){
                that.showQrcode=true;
            }
        })
        
        
        // console.group('===============mounted');
    },
    beforeUpdate() {
        // console.group('  ===============beforeUpdate');
    },
    updated() {
        // console.group('===============updated');
    },
    beforeDestroy() {
        // console.group('  ===============beforeDestroy');
    },
    destroyed() {
        // console.group('===============destroyed');
    },
    methods: {
        //  
        applyGroup(){
            if(this.applyTxt!=''){
                return false
            }
            Addgroup({groId:this.id}).then( res => {
                if(res.code == 1 ) {
                    this.applyTxt='';
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else if(res.code==403){
                    this.applyTxt='';
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }else{
                    this.applyTxt='';
                    this.toastTxt=res.msg;
                    this.mask=true;
                    return setTimeout(() => {
                        this.mask = false;
                    }, 1500);
                }
            })
        },
        followFn(){//
            let appid=this.appid;
            let url=encodeURIComponent(location.href.split('?')[0].split('#')[0]+'?path=applyEntry&id='+this.id+'&istrue=1');
            util.weixinauth(appid,url);
        }   
    }

}




