import { User, Detail } from "@/api/index";
import wx from "weixin-js-sdk";
export default {
  name: "cardDetail",
  data() {
    return {
      name: "",
      imgUrl: "",
      compang: "",
      pushStart: "",
      pushEnd: "",
      total: "",
      time: "",
      que: "",
      item: {},
      mask: false,
      goto: true
    };
  },
  watch: {},
  methods: {
    audioPlay() {
      let that = this;
      for (let x in that.$refs) {
        that.$refs[x][0].pause();
      }
    },
    //
    jumpLink(link) {
      window.location.href = link;
    },
    offBridgeReady() {
      // let u = navigator.userAgent;
      // let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
      // if(isAndroid){
      // WeixinJSBridge.call('showOptionMenu');
      // }
    },
    maskFn() {
      this.mask = false;
    },
    gainUser(userid) {
      let data = {
        userId: userid
      };
      let that = this;
      User(data).then(res => {
        // this.hideLoading();
        that.name = res.data.name;
        that.imgUrl = res.data.imgUrl;
        that.compang = res.data.company;
        that.pushStart = res.data.pushStart;
        that.pushEnd = res.data.pushEnd;
        that.total = res.data.total;
        that.time = res.data.pushDays;
        that.que = res.data.lostCount;
      });
    },
    getDetail(id) {
      let that = this;
      let data = { pkid: id };
      Detail(data).then(res => {
        //  that.list = res.data.list;
        if (res) {
          let classic = res.data.classic || "",
            introspective = res.data.introspective || "",
            thanks = res.data.thanks || "",
            volunteer = res.data.volunteer || "";
          let re = /((http[s]{0,1}|ftp):\/\/[a-zA-Z0-9.-]+.([a-zA-Z]{2,4})(:\d+)?([a-zA-Z0-9\.\-~!@#$%^&*+?:_/=<>]*)?)|(www.[a-zA-Z0-9\.\-]+\.([a-zA-Z]{2,4})(:\d+)?([a-zA-Z0-9\.\-~!@#$%^&*+?:_/=<>]*)?)/g;
          classic = classic.replace(re, function(str) {
            return '<a href="' + str + '">' + str + "</a>";
          });
          introspective = introspective.replace(re, function(s) {
            return '<a href="' + s + '">' + s + "</a>";
          });
          thanks = thanks.replace(re, function(s) {
            return '<a href="' + s + '">' + s + "</a>";
          });
          volunteer = volunteer.replace(re, function(s) {
            return '<a href="' + s + '">' + s + "</a>";
          });
          classic = "<span>" + classic + "</span>"; //
          introspective = "<span>" + introspective + "</span>"; //
          thanks = "<span>" + thanks + "</span>"; //
          volunteer = "<span>" + volunteer + "</span>"; //

          res.data.classicStr = classic;
          res.data.introspectiveStr = introspective;
          res.data.thanksStr = thanks;
          res.data.volunteerStr = volunteer;
          that.item = res.data;
        }
      });
    }
  },
  beforeRouteEnter(to, from, next) {
    if (from.path == "/nav/friend" || from.path == "/nav/record") {
      to.meta.mask = true;
      next();
    } else {
      to.meta.share = true;
      next();
    }
  },
  created() {
    if (this.$route.meta.mask) {
      this.mask = true;
    }
    if (this.$route.meta.share) {
      this.goto = false;
    }
    // WeixinJSBridge.call('showOptionMenu');
  },
  updated() {
    let that = this;
    wx.miniProgram.postMessage({
      data: {
        desc:
          "" +
          that.pushStart +
          "" +
          that.time +
          "" +
          that.total +
          "",
        title: that.name,
        imgUrl: that.imgUrl
      }
    });
    //         let that=this;
    //         let shareUrl=window.location.href.split('?')[0].split('#')[0]+'?path=cardDetail&id='+that.$route.query.id+'&userid='+that.$route.query.userid;
    //         let configData={
    //         	title:that.name, // 
    // 			desc:''+that.pushStart+'     '+that.time+''+that.total+'', // 
    // 			imgUrl:that.imgUrl, // 
    //             link:window.location.href,
    //             success:function(){
    //                 if(that.goto){
    //                     that.mask=false;
    //                     that.$router.go(-1);
    //                 }
    //             }
    // 		};
    // 		let u = navigator.userAgent;
    // 		let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android
    // //		let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios
    //         let obj={
    //             title:that.name+'  '+that.time+''+that.total+'', // 
    // 			imgUrl:that.imgUrl, // 
    //             link:window.location.href,
    //             success:function(){
    //                 if(that.goto){
    //                     that.mask=false;
    //                     that.$router.go(-1);
    //                 }
    //             }
    //         }
    //         if(isAndroid){
    //             configData.link=shareUrl;
    //             obj.link=shareUrl;
    //         }
    // 		wx.onMenuShareAppMessage(configData);// 
    //     	wx.onMenuShareTimeline(obj);//
    //     	wx.onMenuShareQQ(configData);//qq
    //     	wx.onMenuShareQZone(configData);//qq
  },
  mounted() {
    let id = this.$route.query.id,
      userid = this.$route.query.userid;
    this.gainUser(userid);
    this.getDetail(id);
    let that = this;
  },
  destroyed() {
    // console.group('===============destroyed');
  }
};



