import wx from 'weixin-js-sdk'
  export default {
    name:'test',
    data () {
      return {
          recordId:[],
          isRec:true,//
          num:0,//
          serverId:[],
          n:0,//
          txt:''
      }
    },
    computed: {
      
    },
    methods:{
        startRecording() {//
            wx.startRecord();
        },
        obtainRecord(){//
            // let n=that.num;
            // wx.pauseVoice({
            //     localId:that.recordId[n] 
            // });
            let that=this;
            that.isRec=false;
            wx.stopRecord({
                success: function (res) {
                    that.recordId.push(res.localId);
                }
            });
        },
        stopRecord(){//
            let that=this;
            that.isRec=false;
            wx.stopRecord({
                success: function (res) {
                    that.recordId.push(res.localId);
                }
            });
        },
        playRecord(){//
            let that=this;
            if(that.recordId.length==0){
                alert('')
            }else{
                if(that.recordId.length==1){
                    wx.playVoice({
                        localId: that.recordId[0]
                    });
                }else{
                    that.play();       
                }
            }
            
        },
        play(){
            let that=this;
            let n=that.num;
            wx.playVoice({
                localId:that.recordId[n]
            });
        },
        uploadRecord(){
            let that=this;
            if(that.recordId.length==0){
                alert('')
            }else{
                if(that.recordId.length==1){
                    wx.uploadVoice({
                        localId:that.recordId[0], // IDstopRecord
                        isShowProgressTips: 1, // 1
                        success: function (res) {
                           that.serverId.push(res.serverId); // ID
                        }
                    });
                }else{
                    that.upload();       
                }
            }
        },
        upload(){
            let that=this;
            let n=that.n;
            if(that.serverId.length==that.recordId.length){
                //serverId
                that.txt=that.serverId.join(',')
                return
            }
            wx.uploadVoice({
                localId:that.recordId[n],
                isShowProgressTips: 0, // 1
                success: function (res) {
                    that.serverId.push(res.serverId);
                    that.n++;
                    that.upload();
                }
            });
        },
        jump(){
            wx.miniProgram.navigateTo({
                url: '/pages/test/test?id=dasdasda'
            })
        }
    },
    created(){
        let that=this;
        wx.onVoiceRecordEnd({//
            //  complete 
            complete: function (res) {
                that.recordId.push(res.localId);
                wx.uploadVoice({
                    localId:res.localId,
                    isShowProgressTips: 0, // 1
                    success: function (res) {
                        that.serverId.push(res.serverId);
                    }
                });
                wx.startRecord();//
            }
        });
        wx.onVoicePlayEnd({//
            success: function (res) {
                if(that.num==that.recordId.length){
                    that.num=0;
                    return
                }
                that.num++;
                that.play();
            }
        });
        // window.addEventListener("online", function () {  
        //     alert("online");}, true);  
        // window.addEventListener("offline", function () {  
        //     alert("offline");}, true);
        window.ononline = function() {
            alert("");
        }
        window.onoffline = function() {
            alert("");
        }

        
    }
}



